@charset "utf-8";
/**
 * ---reset---
 */

// 去除浏览器外边距

body {
    margin: 0;
    min-height: 100vh;
}

// HTML新增标签元素在IE9一下正确显示
article,
aside,
footer,
header,
nav,
section,
main {
  	display: block;
}


// 去除某些浏览器对图片增加的边框
img {
    border: 0;
}

// 解决浏览器默认的样式导致超链接无法继承其父元素文本样式
a {
    color: inherit;
    background: transparent;
    text-decoration: none;

}

// 解决表单元素内字体样式无法继承的问题，但在IE6，7下无法生效
input,button,textarea,select,optgroup {    
    font-family: inherit;
    font-size: inherit;
    font-style: inherit;
    background: inherit;
}

//移动端表单默认样式
textarea,select,input,button {
    -webkit-appearance: none;
	-moz-appearance: none;
}

textarea {
    resize: none;
}

// 移除元素获取焦点时的外轮廓
:focus {
    outline: 0;
}

// 隐藏滚动条宽度，兼容什么的能吃吗？
::-webkit-scrollbar{
    width: 0;
}

// 选择盒子模型，好想使用"*"统配选择符
* {
    @include box-sizing;
}

/**
 * ---end reset---
 */